import {withRouter} from 'react-router-dom';
import React, {Component, Fragment} from 'react';
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import '../../../assets/Common.css'
import './Infomation.css';
import {Link} from 'react-router-dom';
import axios from 'axios';

import {Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';

import {
    Table,
    Button,
    Dropdown,
    UncontrolledButtonDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Form,
    FormGroup,
    Input,
    Row,
    Col,
    Pagination,
    PaginationItem,
    PaginationLink,
    Card,
    CardBody,
    CardTitle,
    CardHeader,
    CardFooter
} from 'reactstrap';

class BasicInfomantion extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: [],
            modalOpen: false
        };
    }

    componentDidMount() {
        this.loadData()
    }

    componentDidUpdate() {}

    toggleModal = (user) => {
        this.setState({
            modalOpen: !this.state.modalOpen,
            selectedUser: user
        });
    };

    CustomCard = ({label, user}) => {

        var level = 'success';

        if (user.abnormalHrData === '미측정' && user.depressData === '미측정' && user.hrData === '미측정' && user.recoveryData === '미측정' && user.spo2Data === '미측정' && user.stepData === '미측정' && user.stressData === '미측정') {
            level = 'secondary';
        }

        if (user.depressData === '경도' || user.recoveryData === '보통') {
            level = 'warning';
        }

        if (user.abnormalHrData === 'Y' || user.depressData === '심각' || user.depressData === '중증도' || user.hrData > 60 || user.hrData < 50 || user.recoveryData === '낮음' || user.spo2Data > 95 || user.spo2Data < 93 || user.stepData === 0 || user.stressData === 'Y') {
            level = 'danger';
        }

        //console.log('level : ' + level);

        return (
            <div>
                <Card
                    body="body"
                    inverse="inverse"
                    color={level}
                    className='horizontal-card'
                    onClick={() => this.toggleModal(user)}>
                    <CardTitle className="text-white">{label}</CardTitle>
                </Card>
                <Modal
                    isOpen={this.state.modalOpen}
                    toggle={this.toggleModal}
                    className={this.props.className}>
                    <ModalHeader toggle={this.toggleModal}>
                        {
                            this.state.selectedUser && `${this.state.selectedUser.Name}'님의 상세정보`
                        }
                        </ModalHeader>
                    <ModalBody>
                        <p>Hr Data: {this.state.selectedUser && this.state.selectedUser.hrData}</p>
                        <p>Spo2 Data: {this.state.selectedUser && this.state.selectedUser.spo2Data}</p>
                    </ModalBody>
                </Modal>
            </div>
        );
    };

    async loadData() {
        axios
            .get('http://52.78.99.180/api/User/')
            .then(userResponse => {
                const usersWithAdditionalData = [];

                Promise
                    .all(userResponse.data.map(user => {
                        return Promise
                            .all([
                                axios.get(`http://52.78.99.180/api/Hr/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Spo2/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Abnormal_hr/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Stress/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Depress/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Step/${user.User_id}/`),
                                axios.get(`http://52.78.99.180/api/Recovery/${user.User_id}/`)
                            ])
                            .then(([
                                hrResponse,
                                spo2Response,
                                abnormalHrResponse,
                                stressResponse,
                                depressResponse,
                                stepResponse,
                                recoveryResponse
                            ]) => {
                                var lastHrData = hrResponse.data[hrResponse.data.length - 1];
                                var lastSpo2Data = spo2Response.data[spo2Response.data.length - 1];
                                var lastAbnormalHrData = abnormalHrResponse.data[abnormalHrResponse.data.length - 1];
                                var lastStressData = stressResponse.data[stressResponse.data.length - 1];
                                var lastDepressData = depressResponse.data[depressResponse.data.length - 1];
                                var lastStepData = stepResponse.data[stepResponse.data.length - 1];
                                var lastRecoveryData = recoveryResponse[recoveryResponse.data.length - 1];

                                //to do : null 처리
                                lastHrData = lastHrData
                                    ? lastHrData.Hr
                                    : '미측정';
                                lastSpo2Data = lastSpo2Data
                                    ? lastSpo2Data.Spo2
                                    : '미측정';
                                lastAbnormalHrData = lastAbnormalHrData
                                    ? (lastAbnormalHrData.Abnormal_hr === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastStressData = lastStressData
                                    ? (lastStressData.Stress === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastDepressData = lastDepressData
                                    ? lastDepressData.Depress
                                    : '미측정';
                                lastStepData = lastStepData
                                    ? lastStepData.Step
                                    : '미측정';
                                lastRecoveryData = lastRecoveryData
                                    ? lastRecoveryData.Recovery
                                    : '미측정';

                                if (lastDepressData === 3) 
                                    lastDepressData = "심각";
                                else if (lastDepressData === 2) 
                                    lastDepressData = "중증도";
                                else if (lastDepressData === 1) 
                                    lastDepressData = "경도";
                                else if (lastDepressData === 0) 
                                    lastDepressData = "정상";
                                
                                if (lastRecoveryData === 2) 
                                    lastRecoveryData = "높음";
                                else if (lastRecoveryData === 1) 
                                    lastRecoveryData = "보통";
                                else if (lastRecoveryData === 0) 
                                    lastRecoveryData = "낮음";
                                
                                user.Gps_location = user.Gps_location
                                    ? user.Gps_location
                                    : 'no-idea';

                                const userWithAdditionalData = {
                                    ...user,
                                    hrData: lastHrData,
                                    spo2Data: lastSpo2Data,
                                    abnormalHrData: lastAbnormalHrData,
                                    stressData: lastStressData,
                                    depressData: lastDepressData,
                                    stepData: lastStepData,
                                    recoveryData: lastRecoveryData
                                };
                                usersWithAdditionalData.push(userWithAdditionalData);
                            })
                            .catch(
                                error => console.error(`Error fetching data for user ${user.User_id}`, error)
                            );
                    }))
                    .then(() => {
                        this.setState({userData: usersWithAdditionalData});
                        console.log('userData with additional data: ', usersWithAdditionalData);
                    });
            })
            .catch(userError => console.error('Error fetching userData', userError));
    }

    render() {
        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>

                        <div className='whole-page-ui'>
                            <Row>
                                <Col className='font-size-xxlarge vertical-center'>전체 사용자 보기</Col>
                                <Col className='font-size-small vertical-center horizontal-right'>HOME &gt; 전체 사용자 보기</Col>
                            </Row>
                            <div className='space-10px'></div>
                            <div className='boader-black'></div>
                            <div className='space-20px'></div>
                            <Row>
                                <Col md='1' className='ms-auto'>
                                    <Link to={`../../Members/Basic/`}>
                                        <Card className="font-icon-wrapper">
                                            <i className='pe-7s-back'></i>
                                            <p>{'돌아가기'}</p>
                                        </Card>
                                    </Link>
                                </Col>
                            </Row>
                            <Row>
                                {
                                    this
                                        .state
                                        .userData
                                        .map(item => (
                                            <Col key={item.User_id} md={2} className="mb-3">
                                                <this.CustomCard label={item.Name} user={item}/>
                                            </Col>
                                        ))
                                }
                            </Row>
                        </div>
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }
}
export default withRouter(BasicInfomantion);